import router from '../router'
export default {
  name: 'IconList',
  props: { elements: Array, showFavorites: Number },
  data () {
    return {
      favoriteRating: 0
    }
  },
  methods: {
    favoriteChanged (index) {
      // Plot the origin and destiny routes
      console.log('Change in favorite selection')
      console.log('index :' + index + ' Value : ' + this.elements[index].isFavorite)
    },
    showCard (index) {
      if (this.showFavorites === 0) {
        return 1
      } else {
        if (this.elements[index].isFavorite === 0) {
          return 0
        } else {
          return 1
        }
      }
    },
    displayPillData (index) {
      if (this.elements[index].type === 'document') {
        router.push('/pillpdf')
      } else if (this.elements[index].type === 'audio') {
        router.push('/pillaudio')
      } else if (this.elements[index].type === 'video') {
        router.push('/pillvideo')
      }
    }
  }
}
