import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import router from '../router'
export default {
  name: 'LowerBar',
  data () {
    return {
      selection: '/'
    }
  },
  methods: {
    changeRoute () {
      // Plot the origin and destiny routes
      console.log(router.currentRoute.path + '->' + this.selection)
      // Check if the origin route and destiny route are not the same
      if (router.currentRoute.path !== this.selection) {
        // Go to the new route
        router.push(this.selection)
      }
    }
  }
}
